import React, { FC } from "react";
import { Button, Form } from "react-bootstrap";
import { Field, Formik, Form as FormikForm } from "formik";
import { UserType } from "types";

interface IProps {
  values?: UserType | null;
  onSubmit: (values: UserType) => void;
  submitLabel?: string;
}

const INITIAL_VALUES = {
  id: 1,
  firstName: "TestName",
  lastName: "TestLastName",
  username: "testUser",
  email: "test@example.com",
};

export const AddUserForm: FC<IProps> = ({
  values,
  onSubmit,
  submitLabel = "Submit",
}) => {
  return (
    <Formik
      initialValues={values || INITIAL_VALUES}
      onSubmit={(values) => {
        onSubmit(values);
      }}
    >
      <Form as={FormikForm}>
        <Form.Group className="mb-3">
          <Form.Label>Username</Form.Label>
          <Field name="username" placeholder="Enter name" as={Form.Control} />
        </Form.Group>

        <Form.Group className="mb-3">
          <Form.Label>Name</Form.Label>
          <Field name="firstName" placeholder="Enter name" as={Form.Control} />
        </Form.Group>

        <Form.Group className="mb-3">
          <Form.Label>Last Name</Form.Label>
          <Field
            name="lastName"
            placeholder="Enter last name"
            as={Form.Control}
          />
        </Form.Group>

        <Form.Group className="mb-3">
          <Form.Label>Email</Form.Label>
          <Field
            name="email"
            type="email"
            placeholder="name@example.com"
            as={Form.Control}
          />
        </Form.Group>

        <Form.Group>
          <Button type="submit">{submitLabel}</Button>
        </Form.Group>
      </Form>
    </Formik>
  );
};

export default AddUserForm;
